import React from 'react';
import './search.css';

class Search extends React.Component {
    constructor(props) {
        super(props);
        this.handleChange = this.handleChange.bind(this);
    }

    handleChange(event) {
        this.props.stateValue(event.target.value);
        if (this.props.value.length >= 1) {
            this.props.api();
        }
    }

    render() {
        return (
            <section className = 'search' id = 'big'>
                <input type = 'text' placeholder = 'Enter movie name...' onChange = {this.handleChange} ></input>
                <button onClick = {this.props.onClick}>Search</button>
            </section>
        )
    }
}

export default Search;