import React from 'react';
import Element from './element/element'
import bigElement from '../bigElement/bigElement'
import BigElement from '../bigElement/bigElement';

class Elements extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
        }
        this.handleClick = this.handleClick.bind(this);
        this.handleDarkClick = this.handleDarkClick.bind(this);
    }

    handleClick(src, title, overview, date, selector, dark) {
        this.setState({
            bigSrc: src,
            bigTitle: title,
            bigOverview: overview,
            bigReleaseDate: date,
            selector: selector,
            dark: dark
        })
    }

    handleDarkClick() {
        this.setState({dark: '', selector: ''});
    }

    render() {
        return (
            <div>
              <section className = 'element'>
                {this.props.results.map(element => {
                    return element.poster_path && <Element onClick = {this.handleClick} date = {element.release_date} src = {`https://image.tmdb.org/t/p/w500${element.poster_path}`} title = {element.title} overview = {element.overview} name = {element.name}/>
                })}
              </section>
              <BigElement changeDark = {this.handleDarkClick} dark = {this.state.dark} big = {this.state.selector} src = {this.state.bigSrc} date = {this.state.bigReleaseDate} overview = {this.state.bigOverview} title = {this.state.bigTitle}/>
             </div>
        )
    }
}

export default Elements;
