import React from 'react';
import './element.css';
import '../../bigElement/bigElement';
import BigElement from '../../bigElement/bigElement';

class Element extends React.Component {
    constructor(props) {
        super(props);
        this.state = {}
        this.handleClick = this.handleClick.bind(this);
    }

    handleClick(event) {
        const src = this.props.src;
        const title = this.props.title ? this.props.title: this.props.name;
        const date = this.props.date;
        const overview = this.props.overview;
        this.props.onClick(src, title, overview, date, 'showElement', 'showDark');
        document.body.style.overflow = 'hidden';
        document.body.style.height = '100vh';
    }

    render() {
        return (
            <section className = 'element-section' onClick = {this.handleClick}>
                  <a href = '#big'><img src = {this.props.src} alt = {this.props.title}></img>
                  <div className = 'element-title'> 
                    <h1>{this.props.title ? this.props.title : this.props.name}</h1>
                  </div></a>
            </section>
        )
    }
}

export default Element;