import React from 'react';
import './bigElement.css';

class BigElement extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
        }
        this.handleDarkClick = this.handleDarkClick.bind(this);
    }
    handleDarkClick() {
        this.props.changeDark();
        document.body.style.overflow = 'visible';
        document.body.style.height = 'auto';
    }

    render() {
        return (
          <section>
            <div className = {`dark  ${this.props.dark}`} onClick = {this.handleDarkClick}></div>  
            <section className = {`bigElement ${this.props.big}`}>
              <div className = 'bigImage'>
                <img src = {this.props.src}></img>
              </div>
              <div className = 'bigAbout'>
                <h1>{this.props.title}</h1>
                <h2>Overview:</h2><span>{this.props.overview ? this.props.overview : 'Not Available!'}</span>
                <h2>Release Date:</h2><span>{this.props.date ? this.props.date : 'Not Available!'}</span>
              </div>
            </section>
          </section>
        )
    }
}



export default BigElement;