import React from 'react';
import './App.css';
import Search from './Components/Search/search';
import Elements from './Components/elements/elements';

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      value: '',
      results: [1,2,4,5,6]
    }
    this.responseAPI = this.responseAPI.bind(this);
    this.handleValue = this.handleValue.bind(this);
    this.alertfunction = this.alertfunction.bind(this);
  }

  handleValue(value) {
    this.setState({value: value});
  }

  alertfunction() {
    if (this.state.value === '') {
      alert('Enter something!');
    } else {
      this.responseAPI();
    }
  }

  async responseAPI() {
      const url = `https://api.themoviedb.org/3/search/multi?api_key=d8bf019d0cca372bd804735f172f67e8&language=en-US&query=${this.state.value}`
      const response = await fetch(url);
      let data;
      if (response.ok) {
        data =  await response.json()
      } else {
        console.log('Error');
      }
      console.log(data);
      this.setState({results: data.results});
  }

  render() {
    return (
      <div className="App" >
        <Search onClick = {this.alertfunction} stateValue = {this.handleValue} api = {this.responseAPI} value = {this.state.value}/>
        <Elements results = {this.state.results}/>
      </div>
    );
  }
}

export default App;
